// import * as React from "https://cdn.skypack.dev/react@17.0.1";
// import * as ReactDOM from "https://cdn.skypack.dev/react-dom@17.0.1";
import React from "react";



const Timer = () => {
    const [days, setDays] = React.useState(0);
    const [hours, setHours] = React.useState(0);
    const [minutes, setMinutes] = React.useState(0);
    const [seconds, setSeconds] = React.useState(0);

    const deadline = "February, 09, 2023";

    const getTime = () => {
        const time = Date.parse(deadline) - Date.now();

        setDays(Math.floor(time / (1000 * 60 * 60 * 24)));
        setHours(Math.floor((time / (1000 * 60 * 60)) % 24));
        setMinutes(Math.floor((time / 1000 / 60) % 60));
        setSeconds(Math.floor((time / 1000) % 60));
    };

    React.useEffect(() => {
        const interval = setInterval(() => getTime(deadline), 1000);

        return () => clearInterval(interval);
    }, []);

    return (
        <div className="timer" role="timer">
            {/* <div className="col-4">
                <div className="box">
                    <p id="day">{days < 10 ? "0" + days : days}</p>
                    <span className="text">Days</span>
                </div>
            </div> */}
            <div className="col-12">
                <div className="box main-box_count">

                    <span className="box-count">
                        <span id="hour">{hours}</span>
                        <span className="text">HRS</span>
                    </span>
                    <span className="box-count">
                        <span id="minute">{minutes}</span>
                        <span className="text">MINS</span>
                    </span>
                    <span className="box-count">
                        <span id="second">{seconds}</span>
                        <span className="text">SECS</span>
                    </span>
                </div>
            </div>
            {/* <div className="col-4">
                <div className="box">
                    <p id="minute">{minutes < 10 ? "0" + minutes : minutes}</p>
                    <span className="text">Minutes</span>
                </div>
            </div>
            <div className="col-4">
                <div className="box">
                    <p id="second">{seconds < 10 ? "0" + seconds : seconds}</p>
                    <span className="text">Seconds</span>
                </div>
            </div> */}
        </div>
    );
};
export default Timer